import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';
import { map } from 'rxjs/operators';
@Injectable()
export class UserService {
  users = [
    {name: 'user1', description: 'description1'},
    {name: 'user2', description: 'description1'},
    {name: 'user3', description: 'description1'}
  ];
  constructor(private httpClient: HttpClient) {}
  getUsersObservable() {
    return this.httpClient.get('https://randomuser.me/api/?inc=gender,name,picture,location&results=8&nat=gb')
      .pipe(
            map(data => {
                                  return data['results'];
                                }
            )
      );
  }
}
